<template>
  <div>
    <h1 class="title">This is Stolen</h1>
    <h2>Stupid drink name generator</h2>
    <form @submit.prevent="generateCocktailName(form)">
       <p v-if="form.error">
        {{ form.error }}
      </p>
      <div class="selector-wrapper">
        <select class="form-field" v-model="form.rumOrWhiskey">
          <option disabled value="">Rum or Whiskey</option>
          <option>Rum</option>
          <option>Whiskey</option>
        </select>
        <input type="text" class="form-field" placeholder="| Add a Mixer" v-model="form.mixer">
        <input type="text" class="form-field" placeholder="| Add a Garnish" v-model="form.garnish">
      </div>
      <button type="submit" class="generate-btn">Generate your stupid name</button>
    </form>
    <div class="drink-name-wrapper">
      <div class="your-drink">
        <h3>
          {{ stupidName }}
        </h3>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      form: {
        error: "",
        rumOrWhiskey: 'Rum',
        mixer: 'bourbon',
        garnish: 'lemon',
      },
      stupidName: "Here we go"
    }
  },
  methods: {
    getRandomFromBackend () {
        const path = `http://localhost:5000/api/random`
        axios.get(path)
        .then(response => {
          let nameObject = response.data
          this.stupidName = nameObject.adjective + " " + nameObject.noun
        })
      // .catch(error => {
      //   // console.log(error)
      // })
    },
    generateCocktailName (form) {
      if (form.rumOrWhiskey && form.mixer && form.garnish)
      {
        const path = `http://localhost:5000/api/name`
        axios.post(path, form)
        .then(response => {
          let nameObject = response
          this.stupidName = JSON.parse(nameObject.config.data).rumOrWhiskey + " " + JSON.parse(nameObject.config.data).mixer  + " " + JSON.parse(nameObject.config.data).garnish 
        })
        } else {
          this.form.error = 'Looks like you forgot something there'
        }
    },
  },
  created () {
    this.getRandomFromBackend()
  },
}
</script>
